import uniCard from "@/components/uni-card/uni-card.vue"
import api from "@/common/index.js"
export default {
	components: {
		uniCard
	},
	data() {
		return {
			specClass: 'none',
			detailList: [],
			orderInfo: [],
			disData: []

		}
	},
	onLoad(option) {
		this.loadData()
		this.disList();
	},
	methods: {
		back() {
			uni.navigateBack({
				delta: 1
			});
		},
		// 获取商品列表
		loadData() {
			let that = this;
			api.post('/index.php/Home/User/orderView/sn/' + "199991563431562", "", (res) => {
				this.detailList = res.data.list;

				this.orderInfo = res.data.info;

				for (let i in this.detailList) {
					this.detailList[i].url = that.$store.getters.imgShow + "/thumb/" + this.detailList[i].item_no +
						".jpg?x-oss-process=style/300";
				}

			})
		},
		// 获取优惠券
		disList() {
			let that = this;
			api.post("/index.php/Home/Coupon/index", "", function(res) {
				let list = [];
				let allList = res.data;
				allList.forEach(function(item, index) {
					if (item.data.type.not_used == 0) {
						list.push(item)
					}
				})
				that.disData = list;
				that.allList = res.data;
			})
		},
		//提交购物车
		createOrder() {
			let that = this;
			let data = uni.getStorageSync('userinfo');
			let params = {}
			if (this.typeId != "" && parseInt(this.typeId) >= 0) {

				params = {
					uname: data.username,
					src: "APP",
					cls: this.typeId
				}
			} else {
				params = {
					uname: data.username,
					src: "APP"
				}
			}
			api.post("/index.php/Home/Cart/jiesuan", params, function(res) {

				that.$api.msg(that.$t('index').msg3);
				that.loadData();

			})

		},
		//规格弹窗开关
		toggleSpec() {
			if (this.specClass === 'show') {
				this.specClass = 'hide';
				setTimeout(function(){
					this.specClass = 'none';
				}, 250);
			} else if (this.specClass === 'none') {
				this.specClass = 'show';
			}
		},
		stopPrevent() {}
	},

}
